//****************************************
// Graph Tools Element
// DOM Registration: No
// National Instruments Copyright 2017
//****************************************

/**
## ni-graph-tools

> A custom element that provides a graph tools "legend".

Use it to add navigation controls to a graph, chart or intensity graph element.
It provides buttons to switch between pan mode, box zoom mode or to lock navigation.

It also provides a resetOffset button which resets the axes to the range of the data.

Example:
```html
<ni-cartesian-graph graph-ref="g1" value="[[2, 3, 5, 7, 11, 13, 17, 19],
                          [0, 1, 1, 2, 3, 5, 8, 13]]">
  <ni-cartesian-axis show label="Index" axis-position="bottom"></ni-cartesian-axis>
  <ni-cartesian-axis show label="Value" axis-position="left"></ni-cartesian-axis>
</ni-cartesian-graph>

<ni-graph-tools graph-ref="g1" mode="pan"></ni-graph-tools>
```
*/

(function() {
    'use strict';

    var ModeEnum = Object.freeze({
        LOCKED: 'locked',
        PAN: 'pan',
        ZOOM: 'zoom'
    });

    var MODE_ENUM = ModeEnum;
    var $ = NationalInstruments.Globals.jQuery;
    var NI_SUPPORT = NationalInstruments.HtmlVI.NISupport;

    var navigationStates = {};
    navigationStates[MODE_ENUM.LOCKED] = [true, false, false];
    navigationStates[MODE_ENUM.PAN] = [false, true, false];
    navigationStates[MODE_ENUM.ZOOM] = [false, false, true];

    class GraphTools extends NationalInstruments.HtmlVI.Elements.GraphLegend {
        /**### ni-graph-tools properties*/

        /// include ./ni-graph-legend.js
        static get properties() {
            const props = Object.assign({}, super.properties, {
                /** **mode** (string) - the mode of the graph tools. Default: 'pan'.
                Accepted values: 'locked', 'pan', 'zoom'*/
                'mode': {
                    value: MODE_ENUM.PAN,
                    type: 'string'
                }
            });

            return props;
        }

        /**### ni-graph-tools methods*/
        created() {
            super.created();
            this._parentGraph = undefined;
        };

        ready () {
            super.ready();
            this.addListeners();
        }

        attachToGraph(graphElement) {
            if (graphElement.isReady) {
                this._parentGraph = graphElement;
                graphElement.registerGraphTools(this);
            }
        };

        /* The result of user interactions (pan, zoom, ..) are often modified axes
        parameters in the flot datastructures.
        When needed, call this function to update the ni-cartesian-axis elements to have te new values */
        updateCartesianAxesFromFlot() {
            this._parentGraph.axes.forEach(function(axis) {
                if (axis instanceof JQX.CartesianAxis) {
                    axis.syncWithFlot();
                }
            });
        };

        addListeners() {
            var that = this,
                applySelection = function(axis, selection) {
                    var opts = axis.options,
                        offsetBelow = selection.from - axis.min,
                        offsetAbove = selection.to - axis.max;

                    if (opts.offset) {
                        opts.offset.below += offsetBelow;
                        opts.offset.above += offsetAbove;
                    } else {
                        opts.offset = {
                            above: offsetAbove,
                            below: offsetBelow
                        };
                    }
                };

            this.addEventListener('ni-cartesian-graph-box-selected', function(evt) {
                // a rectangle selection was made on the graph, zoom into that selection
                // info about the selection box is passed in the event details (ranges)
                var ranges = evt.detail.originalSource.ranges;
                var plot = that._parentGraph.graph;

                $.each(plot.getAxes(), function (name, axis) {
                    if (axis.options.disableZoom !== true && ranges[name]) {
                        applySelection(axis, ranges[name]);
                    }
                });

                // redraw plot
                plot.setupGrid();
                plot.draw();
                plot.clearSelection();

                this.updateCartesianAxesFromFlot();
            });

            this.addEventListener('ni-cartesian-graph-interaction', function() {
                this.updateCartesianAxesFromFlot();
            });
        }

        attached() {
            super.attached();

            var childElement;

            childElement = document.createElement('div');
            childElement.id = 'div' + NI_SUPPORT.uniqueId();
            childElement.style.width = '100%';
            childElement.style.height = '100%';
            this.appendChild(childElement);
            this.table = document.createElement('table');
            this.table.className = 'ni-graph-tools-box';
            childElement.appendChild(this.table);

            this.createRow(this.table);

            var graph = this.findGraph();
            if (graph !== null && graph instanceof NationalInstruments.HtmlVI.Elements.CartesianGraphBase) {
                this.attachToGraph(graph);
            }
        };

        detached() {
            super.detached();

            this._parentGraph = undefined;
            this.table = undefined;
            $(this).empty();
        }

        /** **asyncProcessAllImagesForComposition** - gathers all the required
        layers of a graph, chart etc (i.e. plot, axes, cursors, legend),
        adds a background color to them and creates a snapshot (as canvas).
        If no background color is specified, it uses the plot background color.
        It returns a promise, which can be used to get the generated snapshot
        image. Using *downloadSnapshot* function, this image can be downloaded
        to disk (see *downloadSnapshot* function below).
        asyncProcessAllImagesForComposition is available, to allow users to
        write their own custom download functions.

    Example:
    ```js
    var asyncSnapshot = function() {
        asyncProcessAllImagesForComposition().then(triggerDownload(), failureCallback);
        _parentGraph.graph.getEventHolder().removeEventListener('onDrawingDone', asyncSnapshot);
    };
    _parentGraph.graph.getEventHolder().addEventListener('onDrawingDone', asyncSnapshot);
    _parentGraph.updateGraphConfig();
    ```
        */
        asyncProcessAllImagesForComposition(userBackgroundColor) {
            var that = this;

            function getDisposeBackgroundSVG(backgroundSVG) {
                return function disposeBackgroundSVG(result) {
                    backgroundSVG.parentNode.removeChild(backgroundSVG);
                }
            }

            function failureCallback() {
            };

            function drawARectangleOnSVG(backgroundSVG, color) {
                var rectangle = document.createElementNS(backgroundSVG, 'rect');
                rectangle.setAttributeNS(null, 'x', 0);
                rectangle.setAttributeNS(null, 'y', 0);
                rectangle.setAttributeNS(null, 'style', 'fill:' + color);
                rectangle.setAttributeNS(null, 'width', backgroundSVG.width.baseVal.value + 'px');
                rectangle.setAttributeNS(null, 'height', backgroundSVG.height.baseVal.value + 'px');
                backgroundSVG.appendChild(rectangle);
            }

            function createBackgroundSVG(backgroundSVG, graphCanvas) {
                const bkBorderWidth = 10,
                    bkBorderWidth2x = bkBorderWidth * 2;

                var pixelRatio = $.plot.browser.getPixelRatio(graphCanvas.getContext('2d'));
                var bkBorderWidth2xScaled = bkBorderWidth2x * pixelRatio;

                document.body.insertBefore(backgroundSVG, document.body.childNodes[0]);

                if (pixelRatio < 1) {
                    backgroundSVG.setAttributeNS(null, 'width', graphCanvas.width / pixelRatio + bkBorderWidth2xScaled);
                    backgroundSVG.setAttributeNS(null, 'height', graphCanvas.height / pixelRatio + bkBorderWidth2xScaled);
                } else {
                    backgroundSVG.setAttributeNS(null, 'width', graphCanvas.width + bkBorderWidth2xScaled);
                    backgroundSVG.setAttributeNS(null, 'height', graphCanvas.height + bkBorderWidth2xScaled);
                }

                backgroundSVG.style.width = graphCanvas.style.width + bkBorderWidth2xScaled;
                backgroundSVG.style.height = graphCanvas.style.height + bkBorderWidth2xScaled;
                backgroundSVG.style.position = 'absolute';

                var svgLeft = graphCanvas.getBoundingClientRect().left + window.scrollX - bkBorderWidth;
                var svgTop = graphCanvas.getBoundingClientRect().top + window.scrollY - bkBorderWidth;
                backgroundSVG.style.left = svgLeft + 'px';
                backgroundSVG.style.top = svgTop + 'px';
            }

            function getSVGFromLegendLayer(graphRef) {
                const containerClassNames = [
                    /*'ni-light-plot-legend',*/
                    'ni-chart',
                    'ni-cartesian-graph',
                    'ni-intensity-graph'
                ];
                var result = null;
                var searchQuery = '';
                for (var i = 0; i < containerClassNames.length; i++) {
                    searchQuery = containerClassNames[i] + '[graph-ref="' + graphRef + '"] svg.legendLayer';
                    result = document.querySelector(searchQuery);
                    if ((result !== null) && (result !== undefined)) {
                        break;
                    }
                }
                return result;
            }

            var graphCanvas = that._parentGraph.graph.getCanvas(),
                backgroundSVG = document.createElementNS('http://www.w3.org/2000/svg', 'svg'),
                graphSVG = that._parentGraph.graph.getSurface().SVGContainer.childNodes[0],
                cursorsFlotOverlay = that._parentGraph.graph.getEventHolder(),
                legendSVG = getSVGFromLegendLayer(that._parentGraph.graphRef),
                sources,
                snapshotBackgroundColor;

            createBackgroundSVG(backgroundSVG, graphCanvas);
            if (userBackgroundColor !== undefined) {
                snapshotBackgroundColor = userBackgroundColor;
            } else {
                snapshotBackgroundColor = window.getComputedStyle(that._parentGraph).backgroundColor;
            }
            drawARectangleOnSVG(backgroundSVG, snapshotBackgroundColor);

            sources = [backgroundSVG, graphCanvas, graphSVG, cursorsFlotOverlay, legendSVG];

            var destinationCanvas = document.createElement('canvas'),
                asynResult = that._parentGraph.takeGraphSnapshot(sources, destinationCanvas);

            return asynResult
                .then(getDisposeBackgroundSVG(backgroundSVG), failureCallback)
                .then(function() {
                    return destinationCanvas;
                }, failureCallback);
        }

        /** **downloadSnapshot** - is a wrapper over *asyncProcessAllImagesForComposition*
        function, used to generate a filename and trigger the actual download of
        a graph snaphot. It also updates the plot dimensions to the page zoom,
        before calling *asyncProcessAllImagesForComposition*. This allows creating
        a high resolution snapshot, simply by zooming the page in.
        Updating the plot dimensions as the page is zoomed in/out, is a requirement,
        to make sure all layers of a snapshot are scaled accordingly.
        As a limitation, it may not be possible to create and download snapshots
        at every refresh of a plot.

    Example:
    ```js
    function takeGraphSnapshotOnClick() {
        downloadSnapshot();
    }
    ```
        */
        downloadSnapshot() {
            var that = this;

            function getCopyCanvasToImg() {
                return function copyCanvasToImgOuter(canvas) {
                    var img = document.createElement('img');
                    img.width = canvas.width;
                    img.height = canvas.height;
                    img.src = canvas.toDataURL('image/png');

                    var downloadBlob = function(blob) {
                        var url = URL.createObjectURL(blob);

                        img.src = url;

                        var downloadLinkElement = document.createElement('A');
                        downloadLinkElement.download = 'Snapshot_' + getDateTimeAsString() + '.png';
                        downloadLinkElement.href = img.src;
                        var clickEvent = new MouseEvent('click');
                        downloadLinkElement.dispatchEvent(clickEvent);
                    };

                    // *** https://stackoverflow.com/questions/9847580/how-to-detect-safari-chrome-ie-firefox-and-opera-browser
                    // Internet Explorer 6-11
                    var isIE = /*@cc_on!@*/false || !!document.documentMode;

                    // Edge 20+
                    var isEdge = !isIE && !!window.StyleMedia;

                    //if ($.plot.browser.isEdge()) {  //use this line after isEdge function is available in browser
                    if (isEdge) {
                        downloadBlob(canvas.msToBlob());
                    } else {
                        canvas.toBlob(downloadBlob);
                    }
                    return canvas;
                }
            }

            //function adapted from https://stackoverflow.com/questions/221294/how-do-you-get-a-timestamp-in-javascript
            function getDateTimeAsString() {
                var currentTime = new Date(),
                    year = currentTime.getFullYear(),
                    month = currentTime.getMonth() + 1, //this returns 0-11, so add a 1
                    date = currentTime.getDate(),
                    hours = currentTime.getHours(),
                    minutes = currentTime.getMinutes(),
                    seconds = currentTime.getSeconds();

                if (month < 10) {
                    month = '0' + month;
                }
                if (date < 10) {
                    date = '0' + date;
                }
                if (hours < 10) {
                    hours = '0' + hours;
                }
                if (minutes < 10) {
                    minutes = '0' + minutes;
                }
                if (seconds < 10) {
                    seconds = '0' + seconds;
                }

                var str = year + '.' + month + '.' + date + '_' + hours + '.' + minutes + '.' + seconds;
                return str;
            }

            function failureCallback() {
            };

            var asyncSnapshot = function() {
                that.asyncProcessAllImagesForComposition().then(getCopyCanvasToImg(), failureCallback);
                that._parentGraph.graph.getEventHolder().removeEventListener('onDrawingDone', asyncSnapshot);
            };
            that._parentGraph.graph.getEventHolder().addEventListener('onDrawingDone', asyncSnapshot);

            that._parentGraph.updateGraphConfig();
        }

        createRow(tbl) {
            var that = this;
            var tr, td1, td2, td3, td4, td5, navNone, btnPan, btnZoom, btnZoomOut, btnSnapshot;

            function setNavButtonsState(locked, pan, zoom) {
                $(navNone).jqxToggleButton({
                    disabled: locked
                });
                $(btnPan).jqxToggleButton({
                    disabled: pan
                });
                $(btnZoom).jqxToggleButton({
                    disabled: zoom
                });
            }

            function setMode(mode) {
                var states = navigationStates[mode];

                if (states) {
                    setNavButtonsState.apply(null, states);
                    that.mode = mode;
                }
            }

            function takeGraphSnapshotOnClick() {
                that.downloadSnapshot();
            }

            tr = document.createElement('tr');
            tbl.appendChild(tr);

            td1 = document.createElement('td');
            td1.className = 'ni-button-box';
            tr.appendChild(td1);
            navNone = document.createElement('button');
            navNone.type = 'button';
            navNone.className = 'ni-locked-icon ni-button';
            $(navNone).jqxToggleButton();
            $(navNone).on('click', function() {
                setMode(MODE_ENUM.LOCKED);
            });
            td1.appendChild(navNone);

            td2 = document.createElement('td');
            td2.className = 'ni-button-box';
            tr.appendChild(td2);
            btnPan = document.createElement('button');
            btnPan.type = 'button';
            btnPan.className = 'ni-pan-icon ni-button';
            $(btnPan).jqxToggleButton();
            $(btnPan).on('click', function() {
                setMode(MODE_ENUM.PAN);
            });
            td2.appendChild(btnPan);

            td3 = document.createElement('td');
            td3.className = 'ni-button-box';
            tr.appendChild(td3);
            btnZoom = document.createElement('button');
            btnZoom.type = 'button';
            btnZoom.className = 'ni-zoom-region-icon ni-button';
            $(btnZoom).on('click', function() {
                setMode(MODE_ENUM.ZOOM);
            });
            td3.appendChild(btnZoom);

            setMode(this.mode); // initialize the "navigation radio butons" in the default mode
            td4 = document.createElement('td');
            td4.className = 'ni-button-box';
            tr.appendChild(td4);
            btnZoomOut = document.createElement('button');
            btnZoomOut.type = 'button';
            btnZoomOut.className = 'ni-zoom-out-icon ni-button';
            $(btnZoomOut).jqxButton({
                disabled: false
            });
            $(btnZoomOut).on('click', function() {
                that._parentGraph.axes.forEach(function(axis) {
                    if (axis.resetOffset) {
                        axis.resetOffset();
                    }
                });

                var plot = that._parentGraph.graph;
                plot.setupGrid();
                plot.draw();
            });
            td4.appendChild(btnZoomOut);

            td5 = document.createElement('td');
            td5.className = 'ni-button-box';
            tr.appendChild(td5);
            btnSnapshot = document.createElement('button');
            btnSnapshot.type = 'button';
            btnSnapshot.className = 'ni-snapshot-icon ni-button';
            $(btnSnapshot).jqxButton({disabled: false});
            $(btnSnapshot).on('click', function() {
                takeGraphSnapshotOnClick(td5);
            });
            td5.appendChild(btnSnapshot);
        };

        allowsScrollWheelZoom() {
            return this.mode === MODE_ENUM.PAN || this.mode === MODE_ENUM.ZOOM;
        };

        allowsPan() {
            return this.mode === MODE_ENUM.PAN;
        };

        sendEventToParentGraph(name) {
            var eventConfig;

            if (this._parentGraph !== undefined) {
                eventConfig = {
                    bubbles: true,
                    cancelable: true,
                    detail: {
                        element: this
                    }
                };

                this._parentGraph.dispatchEvent(new CustomEvent(name, eventConfig));
            }
        };

        propertyChangedHandler(propertyName, oldValue, newValue) {
            super.propertyChangedHandler(propertyName, oldValue, newValue);

            switch (propertyName) {
                case 'graphRef':
                    // should never change
                    break;
                case 'mode':
                    this.sendEventToParentGraph('ni-graph-tools-changed');
                    break;
                default:
                    break;
            }
        };
    };

    JQX('ni-graph-tools', GraphTools);
}());
