/**
## ni-plot-legend

> A custom element that provides a plot legend.

Use it to visualize and change the plots of a graph or chart element.

It provides an expandable list with all the plots of the graphs.

Expand one of the plots and you get a panel where you can control the
parameters of the plot - like plot style, line width, dash style, colors ...

Example:
```html
<ni-cartesian-graph graph-ref="g1" value="[[2, 3, 5, 7, 11, 13, 17, 19],
                          [0, 1, 1, 2, 3, 5, 8, 13]]">
    <ni-cartesian-axis show label="Index" axis-position="bottom"></ni-cartesian-axis>
    <ni-cartesian-axis show label="Value" axis-position="left"></ni-cartesian-axis>
    <ni-cartesian-plot show label="Prime Numbers">
        <ni-cartesian-plot-renderer line-width="2" line-stroke="red">
        </ni-cartesian-plot-renderer>
    </ni-cartesian-plot>
    <ni-cartesian-plot show label="Fibonacci Numbers">
        <ni-cartesian-plot-renderer line-width="2" line-stroke="blue">
        </ni-cartesian-plot-renderer>
    </ni-cartesian-plot>
</ni-cartesian-graph>

<ni-plot-legend graph-ref="g1" mode="pan"></ni-plot-legend>
```
*/

(function() {
    'use strict';

    var NI_SUPPORT = NationalInstruments.HtmlVI.NISupport;
    var $ = NationalInstruments.Globals.jQuery;

    // create a view for a new plot at the end of the legend.
    var createLegendRow = function(legend) {
        legend.createRow(legend.tbl, legend.nPlots);
        legend.helpers.addRenderer(legend.nPlots);
        legend.nPlots++;
    };

    // remove the view for the plot at the specified index
    var removeLegendRow = function(legend, index) {
        legend.removeRow(legend.tbl, index);
        legend.helpers.clearRenderer(index);
        legend.nPlots--;
    };

    class PlotLegend extends NationalInstruments.HtmlVI.Elements.GraphLegend {
        /**### ni-plot-legend properties*/

        /// include ./ni-graph-legend.js
        static get properties() {
            const props = Object.assign({}, super.properties, {
                /** **mode** (string) - the mode of the plot legend. Default: 'full'.
                When in 'compact mode' only the plot icon and the plot name are shown.
                In full mode you can expand the plots and can fully configure them at runtime.

                Accepted values: 'full', 'compact' */
                'mode': {
                    value: 'full',
                    type: 'string'
                }
            });

            return props;
        }

        created() {
            super.created();

            this.tbl = undefined;
            this.nPlots = 0;
            this.currentExpander = null;
            this.helpers = new NationalInstruments.HtmlVI.Elements.PlotLegendHelpers();
            this.plotLegendItemDisplay = new NationalInstruments.HtmlVI.Elements.PlotLegendItemDisplay(this.helpers);
            this.uihelpers = new NationalInstruments.HtmlVI.Elements.LegendHelpers(this.helpers, this.plotLegendItemDisplay);
            this.deferLegendUpdates = false;
        };

        // respond to a plot add (unless the updates are defered)
        plotAdded() {
            if (!this.deferLegendUpdates) {
                createLegendRow(this);
            }
        };

        // respond to a plot remove (unless the updates are defered)
        plotRemoved(index) {
            if (!this.deferLegendUpdates) {
                removeLegendRow(this, index);
            }
        };

        // call syncPlotLegendWithGraph from time to time, when the updates are defered, to
        // synchronize the legend with the current state of the plots in the graph.
        syncPlotLegendWithGraph() {
            if (this.deferLegendUpdates) {
                var graph = this.findGraph();
                if (!graph) {
                    return;
                }

                var graphPlots = graph.plots.length;

                while (graphPlots < this.nPlots) {
                    var index = this.nPlots - 1;
                    removeLegendRow(this, index);
                }

                while (graphPlots > this.nPlots) {
                    createLegendRow(this);
                }
            }
        };

        createRows() {
            var i;
            for (i = 0; i < this.nPlots; i++) {
                this.createRow(this.tbl, i);
            }
        };

        removeRows() {
            var i;
            for (i = this.nPlots - 1; i >= 0; i--) {
                this.removeRow(this.tbl, i);
            }
        };

        throttlePlotUpdates(enableThrottling) {
            var graph = this.findGraph();
            if (graph !== null) {
                graph.throttlePlotUpdates(enableThrottling);
                this.deferLegendUpdates = enableThrottling;
            }
        };

        attachToGraph(graphElement) {
            this.detachFromGraph();
            if (graphElement && graphElement.isReady) {
                this.helpers.graph = graphElement;
                this.nPlots = graphElement.plots.length;
                this.createRows();
                graphElement.registerPlotLegend(this);
            }
        };

        detachFromGraph() {
            this.helpers.clearCache();
            this.helpers.graph = null;
            this.removeRows();
            this.nPlots = 0;
        };

        attached() {
            var childElement,
                that = this;

            super.attached();

            this.helpers.graph = this.findGraph();
            this.helpers.isInEditMode = this.isInEditMode;
            childElement = document.createElement('div');
            childElement.style.width = '100%';
            childElement.style.height = '100%';
            childElement.style.overflowX = 'auto';
            childElement.style.overflowY = 'hidden';
            this.appendChild(childElement);
            this.tbl = document.createElement('table');
            this.tbl.style.width = '100%';
            this.tbl.style.height = '100%';
            childElement.appendChild(this.tbl);

            this.addEventListener('ni-cartesian-plot-attached', function() {
                that.plotAdded();
                that.helpers.clearCache(); // clears the renderer cache
            });

            this.addEventListener('ni-cartesian-plot-detached', function(evt) {
                var index;
                var source = evt.detail.originalSource;
                index = that.helpers.getIndex(source);

                that.plotRemoved(index);
                that.helpers.clearCache(); // clears the renderer cache
            });

            this.addEventListener('ni-cartesian-plot-changed', function(evt) {
                var index;
                var source = evt.detail.originalSource;
                index = that.helpers.getIndex(source);
                that.updatePlotName(index, that.helpers.getPlotName(index));
            });

            this.addEventListener('ni-cartesian-plot-renderer-changed', function(evt) {
                var index;
                var source = evt.detail.originalSource;
                index = that.helpers.rendererToIndex(source); // the event is routed through a plot and we gat a plot as a source
                that.plotLegendItemDisplay.updateShape(index);
            });

            this.addEventListener('ni-cartesian-plot-renderer-attached', function(evt) {
                var index;
                var source = evt.detail.originalSource;

                that.helpers.clearCache(); // clears the renderers cache
                index = that.helpers.rendererToIndex(source); // the event is routed through a plot and we got a plot as a source
                that.plotLegendItemDisplay.updateShape(index);
            });

            this.addEventListener('ni-cartesian-plot-renderer-detached', function(evt) {
                var index;
                var source = evt.detail.originalSource;

                that.helpers.clearCache(); // clears the renderers cache
                index = that.helpers.rendererToIndex(source); // the event is routed through a plot and we got a plot as a source
                that.plotLegendItemDisplay.updateShape(index);
            });

            // update the entries in plot legend based on data series length
            // The legend will apply this updates only at runtime in compact mode
            this.addEventListener('ni-plot-count-changed', function(evt) {
                var source = evt.detail.originalSource;
                if (that.mode === "compact") {
                    while (source.plotCount < that.nPlots) {
                        var index = that.nPlots - 1;
                        removeLegendRow(that, index);
                    }
                    while (source.plotCount > that.nPlots) {
                        createLegendRow(that);
                    }
                }
            });

            var graph = this.findGraph();

            if (graph !== null) {
                this.attachToGraph(graph);
            }
        };

        createRow(tbl, index) {
            var that = this;
            var tr, exp, header, content, div, plotName, p, span;

            tr = document.createElement('tr');
            tbl.appendChild(tr);
            exp = document.createElement('div');
            exp.id = 'expander' + index;
            exp.className = 'ni-plot-legend-box';
            tr.appendChild(exp);
            header = document.createElement('div');
            header.id = 'header' + index;
            header.className = 'ni-master-row';
            exp.appendChild(header);

            div = document.createElement('div');
            div.className = 'ni-plot-display';
            header.appendChild(div);
            this.plotLegendItemDisplay.createHeaderDisplay(div, index);
            this.plotLegendItemDisplay.updateShape(index);

            span = document.createElement('span');
            span.className = 'ni-plot-title';

            plotName = this.helpers.getPlotName(index);
            if (this.mode === 'compact') {
                if (!this.helpers.getPlot(index)) {
                    plotName = 'Plot ' + (index + 1);
                }
            }

            p = document.createTextNode(plotName);
            span.appendChild(p);
            header.appendChild(span);

            content = document.createElement('div');
            content.id = 'content' + index;
            content.className = 'ni-details-box';
            exp.appendChild(content);

            if (this.mode === 'full') {
                $(exp).jqxExpander({
                    expanded: false,
                    disabled: this.isInEditMode,
                    expandAnimationDuration: 50,
                    collapseAnimationDuration: 50,
                    initContent: function() {
                        that.loadContent(content, index);
                    }
                });

                $(exp).on('expanded', function() {
                    if (this !== that.currentExpander) {
                        var expander = that.currentExpander;
                        that.currentExpander = this;
                        if (expander !== null) {
                            $(expander).jqxExpander({
                                expanded: false
                            });
                        }
                    }
                });
            }

            this.applyFont();
        };

        updatePlotName(index, name) {
            var titles = $(this.tbl).find('.ni-plot-title');
            $(titles[index]).text(name);
        };

        loadContent(content, index) {
            var tbl, tr, j, span, p, td1, td2, div;
            content.style.height = ((this.helpers.menu.length + 1) * 30) + 10 + 'px';
            tbl = document.createElement('table');
            tbl.className = 'ni-details';
            content.appendChild(tbl);

            for (j = 0; j < this.helpers.menu.length; j++) {
                var menuItem = this.helpers.menu[j];
                tr = document.createElement('tr');
                tr.className = 'ni-details-row ' + (menuItem.cssClass || '');
                tbl.appendChild(tr);
                td1 = document.createElement('td');
                td1.className = 'ni-detail-row-title-box';
                tr.appendChild(td1);
                span = document.createElement('span');
                span.className = 'ni-detail-row-title';
                td1.appendChild(span);
                var text = NI_SUPPORT.i18n(menuItem.tag);
                if (text.substring(0, 5) === '[msg_') { // in case server is not running
                    text = menuItem.name + '!';
                }

                p = document.createTextNode(text);
                span.appendChild(p);
                td2 = document.createElement('td');
                td2.className = 'ni-details-row-operations-box';
                tr.appendChild(td2);
                switch (menuItem.type) {
                    case 'buttons':
                        this.uihelpers.addButtons(menuItem, td2, index);
                        break;
                    case 'combobox':
                        div = document.createElement('div');
                        div.className = 'ni-selector';
                        td2.appendChild(div);
                        this.uihelpers.addComboBox(menuItem, div, index, 60);
                        break;
                    case 'colorbox':
                        div = document.createElement('div');
                        div.className = 'ni-colorbox-selector ni-selector';
                        td2.appendChild(div);
                        this.uihelpers.addColorBox(menuItem, div, index);
                        break;
                    case 'checkbox':
                        div = document.createElement('div');
                        div.className = 'ni-hover-box';
                        td2.appendChild(div);
                        this.uihelpers.addCheckBox(menuItem, div, index);
                        break;
                }
            }
        };

        removeRow(tbl, index) {
            var row = tbl.children[index];
            this.plotLegendItemDisplay.removeHeaderDisplay(null, index);
            if (row !== null && row !== undefined) {
                tbl.removeChild(row);
            }
        };

        setFont(fontSize, fontFamily, fontWeight, fontStyle, textDecoration) {
            var childElement = this.firstElementChild,
                thisRef = $(this),
                jqrefContent = $(childElement).find(' .jqx-widget-content'),
                jqrefItemTitle = $(childElement).find(' .ni-plot-title'),
                jqrefItemContentTitle = $(childElement).find(' .ni-detail-row-title');

            thisRef.css({
                'font-size': fontSize,
                'font-family': fontFamily,
                'font-weight': fontWeight,
                'font-style': fontStyle
            });

            jqrefContent.css({
                'font-size': fontSize,
                'font-family': fontFamily,
                'font-weight': fontWeight,
                'font-style': fontStyle
            });
            jqrefItemTitle.css({
                'font-size': fontSize,
                'font-family': fontFamily,
                'font-weight': fontWeight,
                'font-style': fontStyle
            });
            jqrefItemContentTitle.css({
                'font-size': fontSize,
                'font-family': fontFamily,
                'font-weight': fontWeight,
                'font-style': fontStyle
            });
        };

        applyFont() {
            var childElement = this.firstElementChild,
                jqrefContent = $(childElement).find(' .jqx-widget-content'),
                jqrefItemTitle = $(childElement).find(' .ni-plot-title'),
                jqrefItemContentTitle = $(childElement).find(' .ni-detail-row-title');

            var fontSize = $(this).css('font-size');
            var fontFamily = $(this).css('font-family');
            var fontWeight = $(this).css('font-weight');
            var fontStyle = $(this).css('font-style');
            jqrefContent.css({
                'font-size': fontSize,
                'font-family': fontFamily,
                'font-weight': fontWeight,
                'font-style': fontStyle
            });
            jqrefItemTitle.css({
                'font-size': fontSize,
                'font-family': fontFamily,
                'font-weight': fontWeight,
                'font-style': fontStyle
            });
            jqrefItemContentTitle.css({
                'font-size': fontSize,
                'font-family': fontFamily,
                'font-weight': fontWeight,
                'font-style': fontStyle
            });
        };

        propertyChangedHandler(propertyName, oldValue, newValue) {
            super.propertyChangedHandler(propertyName, oldValue, newValue);

            switch (propertyName) {
                case 'graphRef':
                    // TODO: add a plot-legend-detached event for parent graph
                    this.helpers.graph.plotLegend = null;
                    var graphElement = this.findGraph();
                    this.attachToGraph(graphElement);

                    break;
                case 'isInEditMode':
                    // this changes once after the element is created
                    this.helpers.isInEditMode = this.isInEditMode;
                    $(this.tbl).find('.ni-plot-legend-box').each(function() {
                        $(this).jqxExpander({
                            disabled: this.isInEditMode
                        });
                    });
                    break;
                default:
                    break;
            }
        };
    };

    JQX('ni-plot-legend', PlotLegend);
}());
